<template>
  <view class="work-container">
	
	<uni-search-bar @confirm="search" placeholder="请输入搜索内容" v-model="params.projectName" radius="100" @clear="clear">
	</uni-search-bar>
	<view class="uni-px-5">
		<uni-data-checkbox mode="tag" selectedColor="green" v-model="params.projectType" :localdata="projectTypes" @change ="typeChange" ></uni-data-checkbox>
	</view>
	<view class="content">
		<uni-table ref="table" :loading="loading" border stripe emptyText="暂无更多数据" @selection-change="selectionChange">
			<uni-tr>
				<uni-th width="80" align="center">年份</uni-th>
				<uni-th width="100" align="center">项目编号</uni-th>
				<uni-th align="center">项目名称</uni-th>
				<uni-th align="center">操作</uni-th>
			</uni-tr>
			<uni-tr v-for="(item, index) in list" :key="index">
				<uni-td align="center">{{item.yearId}}</uni-td>
				<uni-td align="center">{{item.projectNo}}</uni-td>
				<uni-td align="left">
					{{item.projectName}}
				</uni-td>
				<uni-td>
					<uni-tag text="编辑" type="primary" v-if="checkPermi(['archive:project:edit'])" v-on:click="handleUpdate(item.id)" />
					<uni-tag text="删除" type="error" v-if="checkPermi(['archive:project:remove'])" v-on:click="handleDelete(item.id)"/>
				</uni-td>
			</uni-tr>
		</uni-table>
		
		<uni-pagination :total="total" :current="params.pageNum" :pageSize="params.pageSize" :showIcon="true" @change="pageChange" />
		<uni-fab ref="fab" :pattern="pattern" horizontal="left" vertical="bottom"
			direction="horizontal" @fabClick="fabClick" />
		<!-- 提示窗 -->
		<uni-popup ref="alertDialog" type="dialog">
			<uni-popup-dialog :type="alertType" cancelText="取消" confirmText="确认" title="提示" :content="alertContent" @confirm="dialogConfirm"
				@close="dialogClose"></uni-popup-dialog>
		</uni-popup>
	</view>
  </view>
</template>

<script>
import { listProject,getProject,addProject,updateProject,delProject} from "@/api/work/archive/project"
export default {
  data () {
    return {
		// 遮罩层
		loading : false,
		projectTypes: [{
			text: '全部',
			value: ''
		},{
			text: '竣工',
			value: 'ar_project_type_01'
		}, {
			text: '文书',
			value: 'ar_project_type_02'
		}, {
			text: '专业',
			value: 'ar_project_type_03'
		}],
		// 总条数
		total: 0,
		// 查询参数
		params: {
			pageNum: 0,
			pageSize: 6,
			projectName: undefined,
			projectType: '',
		},
		// table 数据
		list: [],
		// 悬浮按钮
		pattern: {
			color: '#7A7E83',
			backgroundColor: '#fff',
			selectedColor: '#007AFF',
			buttonColor: '#007AFF',
			iconColor: '#fff'
		},
		// 弹出框
		alertType:'',
		alertContent:'',
		// 删除Id
		tempId:''
    }
  },
  created () {
  	this.getList();
  },
  onShow(){
	this.getList();
  },
  methods: {
	// 搜索框
	search() {
		this.params.pageNum = 0;
		this.getList()
	},
	// 翻页
	pageChange(e){
		this.params.pageNum = e.current;
		this.getList()
	},
	// 类型选项变更事件
	typeChange(){
		this.getList()
	},
	// 加载数据
	loadData () {
	  this.params.pageNum += 1;
	  this.getList();
	},
	clear() {
		this.params.pageNum = 0;
		this.params.projectName = "";
		this.getList();
	},
	// 查询列表
	getList () {
		const app = this
		app.loading =true
		listProject(this.params).then(res => {
			app.list = res.rows;
			app.total = res.total;
			app.loading =false
		})
	},
	// 悬浮按钮
	fabClick() {
		uni.navigateTo({ url: '/pages/work/archive/project/edit' })
	},
	handleUpdate(id){
		uni.navigateTo({ url: '/pages/work/archive/project/edit?id=' + id })
	},
	handleDelete(id){
		this.alertType = 'warn'
		this.alertContent ='是否确认删除该条数据?'
		this.$refs.alertDialog.open()
		this.tempId = id;
	},
	// 确认删除
	dialogConfirm(){
		const app = this
		if(app.tempId){
			delProject(app.tempId).then(() => {
				app.getList();
			});
		}
	},
	// 取消删除
	dialogClose(){
		this.tempId ="";
	},
  }
}
</script>

<style lang="scss" scoped>
.list-item {
  padding: 20rpx 0;
  border-bottom: 0.5px solid #ccc;
}
</style>